// raycast.js
var Raycast = pc.createScript('raycast');
var uiEntity;
// initialize code called once per entity
Raycast.prototype.initialize = function() {
     uiEntity = this.app.root.findByName("ui");
    if (!this.entity.camera) {
        console.error('This script must be applied to an entity with a camera component.');
        return;
    }

    // Add a mousedown event handler
    document.body.addEventListener("simuleClick",(e)=>{
        this.simuleClick(e);
    });
    this.app.mouse.on(pc.EVENT_MOUSEUP, this.mouseDown, this);

    // Add touch event only if touch is available
    // if (this.app.touch) {
    //     this.app.touch.on(pc.EVENT_TOUCHSTART, this.touchStart, this);
    // }
};

Raycast.prototype.mouseDown = function (e) {
    this.doRaycast(e.x, e.y);
};
Raycast.prototype.simuleClick = function (e) {
    const canvasPos = document.querySelector('canvas').getBoundingClientRect();
    this.doRaycast(e.clientX - canvasPos.x, e.clientY - canvasPos.y);
};

Raycast.prototype.touchStart = function (e) {
    // Only perform the raycast if there is one finger on the screen
    if (e.touches.length === 1) {
        this.doRaycast(e.touches[0].x, e.touches[0].y);
    }
    e.event.preventDefault();
};

Raycast.prototype.doRaycast = function (screenX, screenY) {
    // The pc.Vec3 to raycast from (the position of the camera)
    
    let from = this.entity.getPosition();

    // The pc.Vec3 to raycast to (the click position projected onto the camera's far clip plane)
    let to = this.entity.camera.screenToWorld(screenX, screenY, this.entity.camera.farClip);

    // Raycast between the two points and return the closest hit result
    let result = this.app.systems.rigidbody.raycastFirst(from, to);
    // If there was a hit, store the entity
    if (result) {
        let hitEntity = result.entity;
        if(hitEntity.name != "earth"){
             uiEntity.script.ui.select(hitEntity.parent.dataStore);
        }
       
    }    
};

