// jsonReader.js
var jsonReader = pc.createScript('csvReader');

var jsonDataOriginal = {};
var spotData = {
    minMax : [],
    spots:[]
};

jsonReader.attributes.add('jsonFile', {
    type: 'asset',
    assetType: 'json'
});

jsonReader.attributes.add('distanceMerge', {
    type: 'number',
    default: 1
});
jsonReader.attributes.add('distanceOffset', {
    type: 'number',
    default: 1
});
jsonReader.attributes.add('offset', {
    type: 'number',
    default: 1
});

jsonReader.attributes.add('autoplay', {
    type: 'boolean',
    default: false
});

// initialize code called once per entity
jsonReader.prototype.initialize = function() {

    this.filter={
        usine : true,
        commercial : true,
        rd : true,
        siege:true,
        technique:true,
    }

    if(this.autoplay){
        jsonDataOriginal = this.jsonFile.resource;
        this.start();
    }
    this.app.on("set:json", function (json) {
        jsonDataOriginal = json;

        this.start();
    }, this);

    this.app.on("filter:update",this.updateSpot, this);
};

jsonReader.prototype.start = function(update=false){

    let datas = this.app.root.findByTag("data");
    if(datas.length>0){
        for(let i=datas.length-1;i>=0;i--){
            datas[i].enabled = false;
            datas[i].destroy();
        }
        spotData.spots = []
    }

    this.mergeClose(jsonDataOriginal);
    this.findAverage();
    this.offsetClose();
    this.setColor();
    if(!update){
        var size = this.minMax();
        spotData.minMax = [size.x,size.y];
    }

    this.entity.script.spawner.spawn(spotData);
}
// update code called every frame

jsonReader.prototype.mergeClose = function(jsonData) {
    let json = this.filterData(jsonData.data);
    while(json.length >0) {
        let spot = {
            employee : 0,
            datas :[]
        };
        json[0].lat = parseFloat(json[0].lat);
        json[0].long = parseFloat(json[0].long);
        json[0].employee = parseInt(json[0].employee);
        spot.datas.push(json[0]);
        spot.employee = json[0].employee;
        
        
        for (let y = json.length - 1; y >= 0; y--) {
             if(y!= 0 ){
                 let distance =  new pc.Vec2(parseFloat(json[0].lat),parseFloat(json[0].long)).distance(new pc.Vec2(parseFloat(json[y].lat),parseFloat(json[y].long)));
                 if(distance<this.distanceMerge && json[0].country == json[y].country){
                    json[y].lat = parseFloat(json[y].lat);
                    json[y].long = parseFloat(json[y].long);
                    json[y].employee = parseInt(json[y].employee);
                    spot.employee+=json[y].employee;
                    spot.datas.push(json[y]);
                    json.splice(y,1);
                 }
             }
        }
        
        json.splice(0,1);
        spotData.spots.push(spot);

    }

};
jsonReader.prototype.filterData = function(jsonData) {
    let json = JSON.parse(JSON.stringify(jsonData));
    for (let y = json.length - 1; y >= 0; y--) {
            switch (json[y].site) {
              case 'USINE DE PRODUCTION':
                    if(!this.filter.usine){
                        json[y].employee = 0;
                    }
                break;
              case 'AGENCE COMMERCIALE':
                    if(!this.filter.commercial){
                        json[y].employee = 0;
                    }
                break;
              case 'CENTRE R&D':
                    if(!this.filter.rd){
                        json[y].employee = 0;
                    }
                break;
              case 'SIEGE SOCIAL':
                    if(!this.filter.siege){
                        json[y].employee = 0;
                    }
                break;
              case 'CENTRE TECHNIQUE':
                    if(!this.filter.technique){
                        json[y].employee = 0;
                    }
                break;
              default:
            }
    }
    return json;
};

jsonReader.prototype.updateSpot = function(filter,active){
    if(active == false){
        for(let f in this.filter){
            this.filter[f] = true;
        }
    }else{
        for(let f in this.filter){
           this.filter[f] = false;
        }
        filter = filter.replace('Legend',"");
        this.filter[filter] = true;
    }
    
    this.start(true)
}

jsonReader.prototype.findAverage = function() {
     for(i=0;i<spotData.spots.length;i++){
         let lat  = 0;
         let long = 0;
         
         for(y=0;y<spotData.spots[i].datas.length;y++){
             lat += spotData.spots[i].datas[y].lat;
             long += spotData.spots[i].datas[y].long;
         }
         lat = lat/spotData.spots[i].datas.length;
         long = long/spotData.spots[i].datas.length;
         spotData.spots[i].lat = lat;
         spotData.spots[i].long = long;
     }
};
jsonReader.prototype.offsetClose = function() {
    for(i=0;i<spotData.spots.length;i++){
        for (y=0;y<spotData.spots.length;y++) {
             if(y!= i ){
                var spotI = new pc.Vec2(spotData.spots[i].lat,spotData.spots[i].long);
                var spotY = new pc.Vec2(spotData.spots[y].lat,spotData.spots[y].long);
                var distance =  spotI.distance(spotY);
                 if(distance<this.distanceOffset){
                     let direction = spotI.sub(spotY);
                     direction.normalize();
                     spotData.spots[i].lat += direction.x*this.offset/2;
                     spotData.spots[i].long += direction.y*this.offset/2;
                     spotData.spots[y].lat -= direction.x*this.offset/2;
                     spotData.spots[y].long -= direction.y*this.offset/2;
                 }
             }
        }
     }
};

jsonReader.prototype.setColor = function() {
    for(i=0;i<spotData.spots.length;i++){
        let colorData = {
            usine:0,
            commercial:0,
            rd:0,
            siege:0,
            technique:0,
        };
        for(y=0;y<spotData.spots[i].datas.length;y++){
            let pourc = spotData.spots[i].datas[y].employee/spotData.spots[i].employee;
            switch (spotData.spots[i].datas[y].site) {
              case 'USINE DE PRODUCTION':
                    colorData.usine+=pourc;
                break;
              case 'AGENCE COMMERCIALE':
                    colorData.commercial+=pourc;
                break;
              case 'CENTRE R&D':
                    colorData.rd+=pourc;
                break;
              case 'SIEGE SOCIAL':
                    colorData.siege+=pourc;
                break;
              case 'CENTRE TECHNIQUE':
                    colorData.technique+=pourc;
                break;
              default:
            }
        }
          
        spotData.spots[i].colorData = colorData;
    }
};


jsonReader.prototype.minMax = function() {
    let minMax= new pc.Vec2(99999,0);
    for(i=0;i<spotData.spots.length;i++){
        if(spotData.spots[i].employee<minMax.x){
            minMax.x = spotData.spots[i].employee;
        }
        if(spotData.spots[i].employee>minMax.y){
            minMax.y = spotData.spots[i].employee;
        }
    }
    return minMax;
};
