// custom_shader.js
var CustomShader = pc.createScript('customShader');

CustomShader.attributes.add('vs', {
    type: 'asset',
    assetType: 'shader',
    title: 'Vertex Shader'
});

CustomShader.attributes.add('fs', {
    type: 'asset',
    assetType: 'shader',
    title: 'Fragment Shader'
});

/*============================================================ COLOR */
CustomShader.attributes.add('usineColor', {
    type: 'rgba',
    title: 'Usine color'
});
CustomShader.attributes.add('commercialColor', {
    type: 'rgba',
    title: 'Agence commercial color'
});
CustomShader.attributes.add('rdColor', {
    type: 'rgba',
    title: 'R&D color'
});
CustomShader.attributes.add('siegeColor', {
    type: 'rgba',
    title: 'Siege Social color'
});
CustomShader.attributes.add('techniqueColor', {
    type: 'rgba',
    title: 'Centre technique color'
});

/*============================================================ CHIFFRE */
CustomShader.attributes.add('usine', {
    type: 'number',
    title: 'Usine Number',
    default:0.2
});
CustomShader.attributes.add('commercial', {
    type: 'number',
    title: 'Agence commercial Number',
    default:0.2
});
CustomShader.attributes.add('rd', {
    type: 'number',
    title: 'R&D Number',
    default:0.2
});
CustomShader.attributes.add('siege', {
    type: 'number',
    title: 'Siege Social Number',
    default:0.2
});
CustomShader.attributes.add('technique', {
    type: 'number',
    title: 'Centre technique Number',
    default:0.2
});

// initialize code called once per entity
CustomShader.prototype.initialize = function() {
    this.time = 0;
    
    var app = this.app;    
    
    let wallMaterial = new pc.StandardMaterial().clone(this.entity.model.model.meshInstances[2].material);
   
    // Create a new material and set the shader
    // let wallMaterial = new pc.Material();
    // wallMaterial.shader = this.shader;
    wallMaterial.chunks.diffusePS = this.fs.resource;
    
        wallMaterial.diffuseMap = new pc.Texture(this.app.graphicsDevice, {
            width: 1,
            height: 1,
            format: pc.PIXELFORMAT_R8_G8_B8
        });
    
    wallMaterial.setParameter('uUsineColor', new pc.Vec3(this.usineColor.r,this.usineColor.g,this.usineColor.b).data);
    wallMaterial.setParameter('uCommercialColor', new pc.Vec3(this.commercialColor.r,this.commercialColor.g,this.commercialColor.b).data);
    wallMaterial.setParameter('uRDColor', new pc.Vec3(this.rdColor.r,this.rdColor.g,this.rdColor.b).data);
    wallMaterial.setParameter('uSiegeColor', new pc.Vec3(this.siegeColor.r,this.siegeColor.g,this.siegeColor.b).data);
    wallMaterial.setParameter('uTechniqueColor', new pc.Vec3(this.techniqueColor.r,this.techniqueColor.g,this.techniqueColor.b).data);
    
     wallMaterial.setParameter('uUsine', this.usine);
    wallMaterial.setParameter('uCommercial', this.commercial);
    wallMaterial.setParameter('uRD', this.rd);
    wallMaterial.setParameter('uSiege', this.siege);
    wallMaterial.setParameter('uTechnique', this.technique);

    // Replace the material on the model with our new material
    wallMaterial.update();
    this.entity.model.model.meshInstances[2].material = wallMaterial;
    
    let topMaterial = new pc.StandardMaterial();
    if(this.technique >0){
         topMaterial.diffuse.set(this.techniqueColor.r,this.techniqueColor.g,this.techniqueColor.b);
        // console.log(renders[1].meshInstances[0].material.emissive)
    }else if(this.siege>0){
        topMaterial.diffuse.set(this.siegeColor.r,this.siegeColor.g,this.siegeColor.b);
        // console.log(renders[1].meshInstances[0].material.emissive)
    }else if(this.rd > 0){
         topMaterial.diffuse.set(this.rdColor.r,this.rdColor.g,this.rdColor.b);
        // console.log(renders[1].meshInstances[0].material.emissive)
    }else if(this.commercial >0){
        topMaterial.diffuse.set(this.commercialColor.r,this.commercialColor.g,this.commercialColor.b)
         
    }else{
        topMaterial.diffuse.set(this.usineColor.r,this.usineColor.g,this.usineColor.b);
         
    }
    topMaterial.update();
    this.entity.model.model.meshInstances[0].material = topMaterial;
};
