// spawner.js
var Spawner = pc.createScript('spawner');

var entityCount = 150;
var radius =8.05;
var minScale = 0.2;
var maxScale = 2.5;


// initialize code called once per entity
Spawner.prototype.initialize = function() {
    
};

// update code called every frame
Spawner.prototype.update = function(dt) {

};


Spawner.prototype.spawn = function(json) {
    for(i=0;i<json.spots.length;i++){
        var templateAsset = this.app.assets.get(58515737);
        var instance = templateAsset.resource.instantiate();
        //  console.log(json.spots[i])
        
        var
            lat= json.spots[i].lat,
            lon=json.spots[i].long,
            phi   = (90-lat)*(Math.PI/180),
            theta = (lon+180)*(Math.PI/180);


        var x = -((radius) * Math.sin(phi)*Math.cos(theta)),
            z = ((radius) * Math.sin(phi)*Math.sin(theta)),
            y = ((radius) * Math.cos(phi));
        instance.setLocalPosition(
             x,y,z
        );
        instance.lookAt(new pc.Vec3(0,0,0));
        instance.rotateLocal(90,0 , 0);
        var yScale = json.spots[i].employee.map(json.minMax[0],json.minMax[1],minScale,maxScale);

        // let normalized = json.spots[i].employee /json.minMax[1] ;

        // var yScale = this.Eerp(minScale,maxScale,normalized)
        instance.setLocalScale(0.15,yScale,0.15);
        instance.name="Data-"+i;
        instance.tags.add('data');
    
        instance.script.customShader.usine = json.spots[i].colorData.usine;
        instance.script.customShader.commercial = json.spots[i].colorData.commercial;
        instance.script.customShader.rd = json.spots[i].colorData.rd;
        instance.script.customShader.siege = json.spots[i].colorData.siege;
        instance.script.customShader.technique = json.spots[i].colorData.technique;
        
        instance.dataStore= json.spots[i].datas;
        instance.children[1].collision.halfExtents = new pc.Vec3(0.1, yScale, 0.1);
        
        if(json.spots[i].employee >0){
            this.app.root.addChild(instance);
        }
        
    }
   
};

Spawner.prototype.updateSpawned = function(filter,active){
    console.log(filter,active)
}
Number.prototype.map = function (in_min, in_max, out_min, out_max) {
  return (this - in_min) * (out_max - out_min) / (in_max - in_min) + out_min;
};


Spawner.prototype.Eerp = function (start, end, time) {
     return start*Math.pow((end/start),time);

};


