// ui.js
var Ui = pc.createScript('ui');

Ui.attributes.add('css', {type: 'asset', assetType:'css', title: 'CSS Asset'});
Ui.attributes.add('html', {type: 'asset', assetType:'html', title: 'HTML Asset'});
Ui.attributes.add('countries', {type: 'asset', assetType:'json', title: 'Language Asset'});


var orbit;
var leftMove = false;
var rightMove = false;
var topMove = false;
var bottomMove = false;
var autoRotate = true;
var autoRotateTimeout = null;
var speed = 300;
var language = "en";
Ui.prototype.initialize = function () {
    // create STYLE element
    var style = document.createElement('style');

    // append to head
    document.head.appendChild(style);
    style.innerHTML = this.css.resource || '';
    
    // Add the HTML
    this.div = document.createElement('div');
    this.div.classList.add('containerWebgl');
    this.div.innerHTML = this.html.resource || '';
    
    pc.app.graphicsDevice.canvas.parentNode.appendChild(this.div);
    
    this.div.addEventListener("mousedown",function(e){
        e.stopPropagation();
    });
    this.initEventListener();
    orbit = this.app.root.findByName("Camera").script.orbitCamera;
    // orbit.distance = 80;

    orbit.pitch -= 15;

    this.app.on('lang:change',this.changeLanguage,this)
    this.filterOn = false;
};

Ui.prototype.select= function(data){
    
  var template = document.querySelector("#textrow");

  // On clone la ligne et on l'insère dans le tableau
  var body = document.querySelector(".dataContainer");
    body.innerHTML ="";

    var clone = document.importNode(template.content, true);
    let employee =0,rd=0,usine=0,technique=0,agence=0,social=0;
    for(let i =0;i<data.length;i++){
        if(data[i].employee > 0){
            employee +=  data[i].employee;
        
            
            switch(data[i].site){
                case "SIEGE SOCIAL":
                    social++;
                    clone.querySelector('.social').parentNode.style.display ="block";
                break;
                case 'USINE DE PRODUCTION':
                    usine++;
                    clone.querySelector('.usine').parentNode.style.display ="block";
                break;
                case 'CENTRE R&D':
                    rd++;
                    clone.querySelector('.rd').parentNode.style.display ="block";
                break;
                case 'CENTRE TECHNIQUE':
                    technique++;
                    clone.querySelector('.technique').parentNode.style.display ="block";
                break;
                case 'AGENCE COMMERCIALE':
                    agence++;
                    clone.querySelector('.agence').parentNode.style.display ="block";
                break;
                default:
            }
        }


    }

    if(language == "en"){
        clone.querySelector('.employee').textContent = employee + " Employees";
        clone.querySelector('.usine').textContent = usine + " Factory(ies)";
        clone.querySelector('.agence').textContent = agence + " Commercial agency(ies) ";
        clone.querySelector('.rd').textContent = rd + " R&D center(s)";
        clone.querySelector('.social').textContent = social +" Headquarter(s)";
        clone.querySelector('.technique').textContent = technique + " Technical center(s)";
        clone.querySelector('.country').textContent = this.countries.resource[data[0].country.toUpperCase()];
    }else{
        clone.querySelector('.employee').textContent = employee + " Employés";
        clone.querySelector('.usine').textContent = usine + " Usine de production";
        clone.querySelector('.agence').textContent = agence + " Agence(s) commercial(es)";
        clone.querySelector('.rd').textContent = rd + " Centre(s) R&D";
        clone.querySelector('.social').textContent = social +" Siège(s)";
        clone.querySelector('.technique').textContent = technique + " Centre(s) technique(s)";
        clone.querySelector('.country').textContent = data[0].country.toUpperCase();
    }


    body.appendChild(clone);

};

Ui.prototype.initEventListener = function(){
    // document.querySelector('.scroll_top').addEventListener("mousedown",function(e){
    //     var body = document.querySelector(".dataContainer");
    //     body.scrollTo({
    //        top : body.scrollTop-200,
    //         behavior: "smooth"
    //     }); 
    //     e.stopPropagation();
    // });
    // document.querySelector('.scroll_bottom').addEventListener("mousedown",function(e){
    //     var body = document.querySelector(".dataContainer");
    //     body.scrollTo({
    //        top : body.scrollTop+200,
    //         behavior: "smooth"
    //     }); 
    //     e.stopPropagation();
    // });
    //---------------------------CAMERA
    document.querySelector('.cam_left').addEventListener("click",function(e){
        leftMove = true;
        autoRotate =false;
        clearTimeout(autoRotateTimeout);
        e.stopPropagation();
        setTimeout(reset,200)
    });
    document.querySelector('.cam_right').addEventListener("click",function(e){
        rightMove = true;
        autoRotate =false;
        clearTimeout(autoRotateTimeout);
        e.stopPropagation();
        setTimeout(reset,200)
    });
    
    function reset (e){
       leftMove =false;
        rightMove =false;
        topMove =false;
        bottomMove =false;
        autoRotateTimeout = setTimeout(function(){
            autoRotate =true;
        },3000)
    };

        document.querySelector('.cam_left').addEventListener("touchstart",function(e){
            leftMove = true;
            autoRotate =false;
            clearTimeout(autoRotateTimeout);
            e.stopPropagation();
        });
        document.querySelector('.cam_right').addEventListener("touchstart",function(e){
            rightMove = true;
            autoRotate =false;
            clearTimeout(autoRotateTimeout);
            e.stopPropagation();
        });

        document.querySelector('body').addEventListener("touchend",function(e){
            leftMove =false;
                rightMove =false;
                topMove =false;
                bottomMove =false;
                autoRotateTimeout = setTimeout(function(){
                    autoRotate =true;
                },3000)
        });
    // document.querySelector('.cam_top').addEventListener("mousedown",function(e){
    //     topMove = true;
    //     autoRotate =false;
    //     clearTimeout(autoRotateTimeout);
    //     e.stopPropagation();
    // });
    // document.querySelector('.cam_bottom').addEventListener("mousedown",function(e){
    //     bottomMove = true;
    //     autoRotate =false;
    //     clearTimeout(autoRotateTimeout);
    //     e.stopPropagation();
    // });
    


    this.filters = document.querySelectorAll('.legend li');
    for(filter of this.filters){
        filter.addEventListener('click',(e)=>{this.filterChange(e)})
    }
}

Ui.prototype.filterChange = function(e){
    
    let element = e.target;
    if(e.target.localName == "span"){
        element = e.target.parentNode;
    }
    
    if(this.filterOn && element.classList.contains('inactive')){
        for(f of this.filters){
            f.classList.add('inactive')
        }
        element.classList.remove('inactive')
    }else if(this.filterOn && !element.classList.contains('inactive')){
        this.filterOn = false;
        for(f of this.filters){
            f.classList.remove('inactive')
        }
    }else{
        this.filterOn = true;
        for(f of this.filters){
            f.classList.add('inactive')
        }
        element.classList.remove('inactive')
    }
    
    this.app.fire('filter:update',element.id,this.filterOn)
}
// update code called every frame
Ui.prototype.update = function(dt) {
    if(leftMove){
        orbit.yaw -= dt*speed;
    }else if(rightMove){
        orbit.yaw += dt*speed;
    }else if(topMove){
         orbit.pitch -= dt*speed;
    }else if(bottomMove){
        orbit.pitch += dt*speed;
    }else if(autoRotate){
        orbit.yaw += dt*5;
    }
};

Ui.prototype.changeLanguage = function(lang){
    language = lang;
    if(lang == "en"){
            document.querySelector('.usineName').textContent = "Factory(ies)";
            document.querySelector('.commercialName').textContent = "Commercial agency(ies) ";
            document.querySelector('.rdName').textContent = "R&D center(s)";
            document.querySelector('.siegeName').textContent = "Headquarter(s)";
            document.querySelector('.techniqueName').textContent = "Technical center(s)";
    }else{
            document.querySelector('.usineName').textContent = "Usine(s) de production";
            document.querySelector('.commercialName').textContent = "Agence(s) commercial(es)";
            document.querySelector('.rdName').textContent = "Centre(s) R&D";
            document.querySelector('.siegeName').textContent = "Siège(s)";
            document.querySelector('.techniqueName').textContent = "Centre(s) technique(s)";
    }
}

